import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import axios from "axios";
import Navbar from "@/components/Navbar";
import ProtectedImage from "@/components/ProtectedImage";

const Profile = () => {
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [error, setError] = useState("");

  useEffect(() => {
    const fetchUserProfile = async () => {
      const token = localStorage.getItem("token");

      if (!token) {
        setError("Token tidak ditemukan, silakan login.");
        return;
      }

      try {
        const response = await axios.get("http://localhost:3000/api/profile", {
          headers: { Authorization: `Bearer ${token}` },
        });

        setUser(response.data);
      } catch (err) {
        setError(err.response?.data?.message || "Terjadi kesalahan.");
        localStorage.removeItem("token"); // Hapus token jika tidak valid
      }
    };

    fetchUserProfile();
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("token");
    router.push("/");
  };

  return (
    <div className="bg-black">
      <Navbar />
      <div className="bg-black h-screen flex justify-center items-center">
        <div>
          <ProtectedImage
            src="/branding/logo.png"
            className="w-[128px] ml-5 mb-5"
            alt="JawirAI Logo"
          />

          <h1 className="text-white ml-5 text-xl mb-3">User Profile</h1>

          {error ? (
            <p className="text-red-500">{error}</p>
          ) : user ? (
            <div className="p-5 w-[340px] xs:w-[390px] border border-white/15 rounded-3xl">
              <p className="text-white">{user.name}</p>
              <p className="text-white">{user.email}</p><br />
              <button
                className="p-3 bg-red-500 rounded-full text-white text-xs"
                onClick={handleLogout}
              >
                Logout
              </button>
            </div>
          ) : (
            <p>Loading...</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default Profile;